import {Inject} from 'angular-es-utils';

import defaultHeaderTpl from './tpls/default-header.tpl.html';
import defaultCellTpl from './tpls/default-cell.tpl.html';
import checkboxHeaderTpl from './tpls/checkbox-header.tpl.html';
import checkboxCellTpl from './tpls/checkbox-cell.tpl.html';
import emptyGridTipTpl from './tpls/empty-grid-tip.tpl.html';

/**
 * ,
 */
@Inject('$rootScope')
export class $Grid {

	constructor($rootScope) {
		this.$rootScope = $rootScope;
	}

	fillOpts(gridOptions) {

		let DEFAULT_CONFIGS = {
			resource: null, // $resource
			response: null, // response
			queryParams: null, // 
			columnsDef: [],   // , field: displayName: cellTemplate: align:
			externalData: null, // 
			showPagination: true, // 
			headerTpl: null, // ,  or url
			cellTpl: null, // ,  or url
			pager: {
				totals: 0,  // 
				totalPages: 1,  // 
				pageNum: 1,  // 
				pageSize: 20, // 
				pageSizeList: [10, 15, 20, 30, 50]
			}
		};

		return Object.assign(gridOptions, Object.assign(DEFAULT_CONFIGS, gridOptions));

	}

	refresh(gridOptions, queryParams) {

		const gridOpts = this.fillOpts(gridOptions);

		const pageParams = {
			pageNum: gridOpts.pager.pageNum,
			pageSize: gridOpts.pager.pageSize
		};

		queryParams = Object.assign({}, gridOpts.queryParams, queryParams);

		// 
		if (!gridOpts.externalData) {

			gridOpts.resource.get(Object.assign(pageParams, queryParams)).$promise

				.then(res => {

					gridOptions.response = res;
					gridOpts.data = res.list;

					let pager = gridOpts.pager;

					pager.pageNum = res.pageNum || 1;
					pager.pageSize = res.pageSize || 20;
					pager.totals = res.totals || 0;
					pager.totalPages = Math.ceil((res.totals || 0) / pager.pageSize);

					gridOpts.asyncChanged = true;

					// $httpapplyjs
					this.$rootScope.$$postDigest(() => {
						gridOpts.pager.asyncChanged = false;
					});

				});
		}
	}

}

/**
 * ,
 * @warning ,, NEWBI_SORTABLE_TEMPLATE
 */
export let GridTemplates = {
	DEFAULT: [defaultHeaderTpl, defaultCellTpl, emptyGridTipTpl],
	SELECTABLE: [checkboxHeaderTpl, checkboxCellTpl, emptyGridTipTpl]
};



