import './_modal.scss';

import angular from 'angular';
import {Inject} from 'angular-es-utils';

import modalTemplate from './modal.tpl.html';
import confirmTemplate from './confirm.tpl.html';
import Modal from './Modal';
import CONSTANT from './Constants';

const noop = () => {};

/**
 * modal
 */
@Inject('$document', '$compile', '$controller', '$rootScope', '$q', '$templateRequest')
export default class ModalService {

	constructor($document, $compile, $controller, $rootScope, $q, $templateRequest) {
		this.$document = $document;
		this.$compile = $compile;
		this.$rootScope = $rootScope;
		this.$controller = $controller;
		this.$q = $q;
		this.$templateRequest = $templateRequest;
	}

	/**
	 *
	 * {
	 *  scope:  {Scope} modalscope,$rootScope
	 *  title:  {string} modal
	 *  style:  {object} modal,
	 *  fullscreen:   {boolean} ,
	 *  hasFooter: {boolean} ,
	 *  body:   {string} url,modal
	 *  locals: {object} modal,
	 *  controller: {function|string} 
	 *  controllerAs: {string} ,$ctrl
	 *  bindings: {boolean|Object} truescope,Object()
	 * }
	 *
	 * @returns {Modal}
	 */
	modal({scope, title, style, fullscreen = false, hasFooter = true, body, locals, controller, controllerAs = '$ctrl', bindings, onClose = noop}) {

		let modalElement = angular.element(modalTemplate);
		let modalHTMLElement = modalElement[0];

		// scope,scopemodal,rootScope
		const providedScope = scope || this.$rootScope;
		let modalScope = providedScope.$new();
		// scope,scope
		modalHTMLElement.$scope = modalScope;

		let renderDeferred = this.$q.defer();
		let modalInstance = new Modal(modalHTMLElement, renderDeferred);

		// 
		this.$templateRequest(body).then(tplStr => {

			// modalscope
			modalScope.title = title;
			modalScope.style = style;
			modalScope.fullscreen = fullscreen;

			// ,.ng-if$compile,
			if (!hasFooter) {
				const footer = modalHTMLElement.querySelector('footer');
				footer.parentNode.removeChild(footer);
			}

			// scoep
			modalScope.$ok = modalInstance.ok.bind(modalInstance);
			modalScope.$cancel = modalInstance.cancel.bind(modalInstance);
			modalScope.$close = () => {
				modalInstance.close();
				onClose();
			};

			if (controller) {

				const ctrlLocals = Object.assign({
					$scope: modalScope,
					$element: angular.element(modalHTMLElement.querySelector('.container')),
					modalInstance: modalInstance
				}, locals);

				let ctrlInstance;

				// controller,($controllertrue,private api),holdhhh
				// @see https://github.com/angular/angular.js/blob/master/src/ng/controller.js#L126
				let ctrlInstantiate = this.$controller(controller, ctrlLocals, true);

				// controller,
				ctrlInstance = ctrlInstantiate.instance;

				// scopecontroller
				if (bindings) {

					let bindingsForController = angular.isObject(bindings) ? bindings : providedScope;
					Object.assign(ctrlInstance, bindingsForController);
				}

				// controller
				ctrlInstance = ctrlInstantiate();
				// controllerscope
				ctrlInstance.ok && (modalScope.$ok = ctrlInstance.ok.bind(ctrlInstance));
				ctrlInstance.cancel && (modalScope.$cancel = ctrlInstance.cancel.bind(ctrlInstance));
				ctrlInstance.close && (modalScope.$close = ctrlInstance.close.bind(ctrlInstance));

				modalScope[controllerAs] = ctrlInstance;

				if (angular.isFunction(ctrlInstance.$onInit)) {
					ctrlInstance.$onInit();
				}
			}

			angular.element(modalHTMLElement.querySelector(CONSTANT.BODY_SELECTOR)).append(tplStr);

			renderDeferred.resolve(this.$compile(modalElement)(modalScope));
		});

		return modalInstance;
	}

	confirm(message, {onClose = noop} = {}) {

		let confirmElement = angular.element(confirmTemplate);
		let confirmDOMElement = confirmElement[0];

		let renderDeferred = this.$q.defer();
		let confirmInstance = new Modal(confirmDOMElement, renderDeferred);

		let scope = this.$rootScope.$new();
		scope.message = message;
		scope.$ok = confirmInstance.ok.bind(confirmInstance);
		scope.$cancel = confirmInstance.cancel.bind(confirmInstance);
		scope.$close = () => {
			confirmInstance.close();
			onClose();
		};

		this.$compile(confirmDOMElement.querySelector(CONSTANT.CONFIRM_CONTAINER))(scope);

		renderDeferred.resolve();
		// confirmresizable
		confirmInstance.open = confirmInstance.open.bind(confirmInstance, false);

		return confirmInstance;

	}

}



