import {Inject} from 'angular-es-utils';

/**
 * <pre>
 *     ,
 *     module: ccms.components.form
 *     service: $Validator
 * </pre>
 */
@Inject('$q')
export default class ValidatorService {

	/**
	 * @param $q
	 * @private
	 */
	constructor($q) {
		/** @private */
		this.$q = $q;
	}

	/**
	 * 
	 * @requires ValidatorsDirective ValidatorDirective
	 * @param {controller} formCtrl .namescope
	 * @return {Promise} promise,resolve,reject
	 */
	validate(formCtrl) {

		let deferred = this.$q.defer();
		formCtrl.$$validatedDefer = deferred;

		// ngModel
		formCtrl.$$invalidCount = formCtrl.$$ngModelCtrls.filter(ctrl => ctrl.$invalid).length;

		formCtrl.$$ngModelCtrls.forEach(ctrl => {
			ctrl.$validate();
		});

		return deferred.promise;
	}

}



