/**
 *  $scope.$emit $scope.$on ()
 */
export default class Mediator {

	constructor() {
		this.topics = {};
	}

	/**
	 * 
	 * @param topic 
	 * @param listener 
	 * @param scope scopescope
	 * @returns {Function} 
	 */
	subscribe(topic, listener, scope) {

		let topicListeners = this.topics[topic] = this.topics[topic] || [];

		// topic
		function unSubscribe(listener) {

			let listenerIndex;

			if (!listener) {
				// 
				topicListeners.length = 0;
			} else {

				listenerIndex = topicListeners.indexOf(listener);
				if (~listenerIndex) {
					topicListeners.splice(listenerIndex, 1);
				}
			}
		}

		if (scope && (scope.constructor === scope.$root.constructor)) {
			// scope
			scope.$on('$destroy', unSubscribe.bind(null, listener));
		}

		topicListeners.push(listener);

		return unSubscribe;
	}

	/**
	 * 
	 */
	publish(...args) {

		let topic = args[0];
		let listeners = this.topics[topic] || [];
		let slice = Array.prototype.slice;

		listeners.forEach(listener => {

			if (typeof listener === 'function') {
				listener.apply(null, slice.call(args, 1));
			} else {
				console.error(' %s listener', topic);
			}
		});

		return this;
	}

}



